body {
    height: 100%;
    box-sizing: border-box;
    font-family: 'Muli', sans-serif;
    font-weight: 100;
    margin: 0;
    background-color: #4a76a8aa;
    padding: 5px 0 0 0;
    color: #000;
    overflow-y: scroll;
}

a, button {
    cursor: pointer;
}

a {
    text-decoration: none;
    color: #000;
}

#root {
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas:
    "h h"  "n c";
    flex: 1 0 auto;

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.pageArea {
    padding: 10px 25px;
    border-radius: 5px;
    background-color: #fff;
    margin-bottom: 5px;
}

.app-wrapper-content {
    grid-area: c;
    margin-top: 5px;
    margin-left: 5px;
    border-radius: 5px;
}

.btn {
    background-color: #4a76a8;
    color: #fff;
    border: 0;
    border-radius: 2px;
    outline: none;
    height: 32px;
    padding: 5px 17px;
    margin-left: 10px;
    cursor: pointer;
    transition: all .3s;
}

.btn:hover{
    background-color: #4A76C9;
}

input:not([type="checkbox"]){
    padding: 5px 8px;
    width: 200px;
    font-size: 15px;
}

textarea {
    overflow: hidden;
}

/*News popUp styles*/

.minimized {
    width: 300px;
    cursor: pointer;
    border: 1px solid #FFF;
}

.minimized:hover {
    border: 1px solid yellow;
}

/* увеличенная картинка */
#magnify {
    display: none;
    position: fixed;
    max-width: 380px;
    height: auto;
    z-index: 9999;
    width: 100%;
}

.magnify {
    display: block !important;
    left: 37%;
    top: 70px;
}

#magnify img {
    width: 400px;
    height: 600px;
    border-radius: 5px;
}

/* затемняющий фон */
#overlay {
    display: none;
    background: #000;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0.8;
    z-index: 9990;
}

.overlay {
    display: block !important;
}

/* кнопка закрытия */
#close-popup {
    width: 30px;
    height: 30px;
    background: #FFFFFF;
    border: 1px solid #AFAFAF;
    border-radius: 15px;
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 15px;
}

#close-popup i {
    width: 30px;
    height: 30px;
    background: url(https://codernote.ru/files/cross.png) no-repeat center center;
    background-size: 16px 16px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}

.pop_overflow {
    overflow: hidden;
    padding-right: 17px !important;
}

.popupLayer {
    height: 3px;
    width: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: #4a76a8;
    -webkit-animation-name: popupLayer;
            animation-name: popupLayer;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes popupLayer {
    0% {
        width: 0;
    }
    100% {
        width: 400px;
    }
}

@keyframes popupLayer {
    0% {
        width: 0;
    }
    100% {
        width: 400px;
    }
}




